






import Vue from 'vue'
import Component from 'vue-class-component'
  
  @Component
  export default class Motto extends Vue {
    private motto: string = ''

    mounted () {
      this.axios.get('https://v1.hitokoto.cn')
      .then((res: any) => {
        console.log(res)
        const resData = res.data
        if (res.status == 200) {
          this.motto = `${resData.hitokoto}  ${resData.from}`
        }
      })
    }
  }
