










import Vue from 'vue'
import Main from './components/Main.vue'
import Snow from './components/Snow.vue'
import BGContainer from './components/BGContainer.vue'
import Component from 'vue-class-component'

@Component({
  components: {
    Main,
    Snow,
    BGContainer
  }
})
export default class App extends Vue{
  constructor () {
    super()
  }

  mounted () {
    // 
    document.onkeydown = (e) => {
      switch (this.$store.state.mode) {
        case 0: {
          // 
          // Esc0
          // Enter1
          // 2
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            this.$store.commit('changeMode', { mode: 1 })
          }
          if (e.keyCode == 32) {
            this.$store.commit('changeMode', { mode: 2 })
          }
          break
        }
        case 1: {
          // 
          // Esc0
          // Enter
          // 
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            // 
            this.$store.commit('doSearch')
          }
          break
        }
        case 2: {
          // 
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            this.$store.commit('changeMode', { mode: 1 })
          }
          if (e.keyCode == 32) {
            this.$store.commit('changeMode', { mode: 2 })
          }
          break
        }
        case 3: {
          // 
          // Esc0
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
        }
        case 4: {
          // 
          // Esc0
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
        }
      }
    }
    //  store 
    this.$store.dispatch('initialization')
  }
}
